import React from "react";
import classes from "../../../styles.module.scss";

const ContactsModal = ({profile}) => {

    return <div className={classes.contactsWrapper} style={wrapperStyles}>
        <b>Contacts: </b> {Object.keys(profile.contacts).map(key => {
        if (profile.contacts[key]) {
            return <Contact key={key} contactTitle={key} contactValue={profile.contacts[key]}/>
        }
    })}
    </div>
}

export default ContactsModal

const Contact = props => {
    return <>
        <div>
            {props.contactTitle}: <a href={props.contactValue} target={'_blank'}>{props.contactValue}</a>
        </div>
    </>
}

const wrapperStyles = {
    position: 'absolute',
    top: 0,
    left: '45%',
    bottom: '50%',
    right: '50%',
    minWidth: '50%',
    minHeight: '150%',
    backgroundColor: '#2fa774',
    border: '2px solid black',
    padding: '15px',
    borderRadius: '10px',
    boxShadow: '0 0 8px 1px black'
}